.btn-delete {
    display: inline-block;
    opacity: 0;
    background: #fff;
    color: #444;
    font-weight: bold;
    border: 1px solid silver;
    border-radius: 100px;
    position: absolute;
    top: -1.3rem;
    height: 2.6rem;
    width: 2.6rem;
    left: -1.3rem;
    -webkit-transition: opacity 0.4s;
    -o-transition: opacity 0.4s;
    transition: opacity 0.4s;
}
.btn-delete:active {
    background: #ddd;
    color: #222;
}
#grid {
    display: -ms-flexbox;
    display: flex;
    list-style-type: none;
    padding: 0;
    margin: auto;
    overflow-y: hidden;
    -ms-flex-flow: wrap;
        flex-flow: wrap;
    padding: .4rem;
}
#grid> li {
    position: relative;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: end;
        align-content: flex-end;
    -ms-flex-direction: column;
        flex-direction: column;
    width: 24rem;
    min-width: 12rem;
    margin: 1.2rem;
    padding: .8rem;
    padding-bottom: 0;
    text-align: center;
    border: 1px solid silver;
    background-color: #FFF;
}
#grid> li:hover {
    outline: .3rem solid #CCC;
    cursor: pointer;
}
#grid> li:hover .btn-delete {
    opacity: 1;
}
#grid .picture {
    min-height: 16rem;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    -ms-flex-positive: 1;
        flex-grow: 1;
    background-color: #000;
}
#grid .picture img {
    width: 100%;
    display: block;
}
#grid span {
    padding: 1.6rem;
    display:block;
    color: #000000BB
}
#grid a{
    text-decoration: none;
}



